.vh {
  visibility: hidden;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.desktop-only {
  display: none !important;
}

@media screen and (min-width: 980px), print {
  .desktop-only {
    display: block !important;
  }
}

.tablet-only {
  display: none !important;
}

@media screen and (min-width: 768px), print {
  .tablet-only {
    display: block !important;
  }
}

.mobile-only {
  display: none !important;
}

@media (max-width: 767px) {
  .mobile-only {
    display: block !important;
  }
}

@media screen and (min-width: 980px), print {
  .no-desktop {
    display: none !important;
  }
}

@media screen and (min-width: 768px), print {
  .no-tablet {
    display: none !important;
  }
}

@media (max-width: 767px) {
